<template>
	<!-- 我的 -->
	<view class="profile-head">
		<view class="pr_floor_item">
		<!-- 个人信息开始 -->
		<view class="hi">
		<view class="profile_head_floor">
			<view class="pr_person_detail">
				<view class="pr_person_pic">
					<navigator url="/src/pages/level3/profile/userhead">
					<img v-bind:src="he.userHeader" mode="widthFix"></img></navigator>
				</view> 
				<view class="pr_admin_detail">
					<navigator url="/src/pages/level2/profile/edit"><view class="pr_admin_name">{{he.userName}}
					<image class="pr_floor_typeadd"   src="../../../static/icons/profile/edit.png" mode="widthFix"  style="width: 20px; vertical-align: middle; display: inline-block"></image>
					</view></navigator>
					<view class="pr_admin_signature">{{he.motto}}</view>
				</view>
			</view>
		
		</view>
		<!-- 个人信息结束 -->
		<!-- 标签开始 -->
		<view class="pr_floor_type">
			<view  v-for="(item,index) in itemList" :key="index">
				<text >{{item}}</text>
				</view>
		  <navigator url="/src/pages/level2/message/label">
		  <image class="pr_floor_typeadd"   src="../../../static/icons/profile/add.png" mode="widthFix"  style="width: 20px; vertical-align: middle; display: inline-block"></image>
		  </navigator>
		</view>
		<!-- 标签结束 -->
		</view>	
		<!-- /* 信息浏览【赞/关注/被关注数目】开始 */ -->
		<view class="floor_ProfileOption">
			<view class="give_like" >
				<navigator url="/src/pages/level2/profile/praise">
				<text>赞</text>
				<text>{{he.likedNum}}</text></navigator>
			</view>
			<view class="attention"  >
				<navigator url="/src/pages/level2/profile/followee">
				<text>关注</text>
				<text>{{he.followNum}}</text></navigator>
			</view>
			<view class="like">
				<navigator url="/src/pages/level2/profile/myfans">
				<text>被关注</text>
				<text>{{he.followedNum}}</text></navigator>
			</view>
		</view>	
		<!-- /* 信息浏览【赞/关注/被关注数目】结束 */ -->
	    </view>
	</view>
</template>

<script>
	import {
	 getUserLabels,
	} from "../../../src/axios/profile/profile.js";
	export default {
		name:'ProfileHead',
		data() {
			return {
				itemList:[],
				myuid:1,
			};
		},
		props: {
		  he: {
		    type: Object,
		    default() {
		      return {};
		    },
		      },
		        },
		
		methods:{
		//获取用户uid
		getUid(){
				 var a = '';
				 uni.getStorage({
				 	key:'userInfo',
				 	success: function(res) {
				 		a = res.data;
						window.myuid=a.uid;
				 	}
				 });
		},
		getUserLabels(uid,page,size){
			return getUserLabels(uid,page,size).then(
			(res) => {
				for(let i=0;i<res.data.data.detail.length;i++){
					// console.log(res.data.data.detail[i].content);
					this.itemList.push(
						res.data.data.detail[i].content,
					);
					console.log(this.itemList);
				}
			      // this.labelContent=itemList.data.detail
				},
			(err) => {
			  console.log(err);
			}
			);
		},
		},
		mounted() {
			this.getUid();
			this.getUserLabels(myuid,1,10)
			//要用this欧哈哈哈哈
		},
		};
</script>

<style scoped>
	.pr_floor_item {
	  padding:0 23px 0 16px;
	 border-bottom: 2px solid #ececec;
	  padding-bottom: 10px;
	}
	/* <!-- 个人信息开始 --> */
	.profile_head_floor{
		margin: 20rpx 0 24rpx 0;
		height: 60px;
		width: 100%;
	}
	.pr_person_detail {
	  height: 80rpx;
	  width: 80rpx;
	  float: left;
	  display: flex;
	  align-items: center;
	}
	.pr_person_pic {
	  flex: 1;
	}
	.pr_person_pic img {
		margin-top: 15px;
	  vertical-align: middle;
	  box-sizing: border-box;
	  width: 66px;
	  height: 66px;
	  border: 1px solid #8f8f94;
	  border-radius: 80rpx;
	}
	
	.pr_admin_detail {
	  margin-left: 14px;
	  width: 100%;
	  flex: 1;
	  display: flex;
	  flex-direction: column;
	  justify-content: flex-start;
	}
	.pr_admin_name {
	  /* height: 40rpx; */
	  width: 150px;
	  line-height: 19px;
	  display: inline-block;
	  font-size: 20px;
	  color: black;
	}
	.pr_admin_signature {
	  /* height: 40rpx; */
	  line-height: 12px;
	  padding-top: 8px;
	  display: inline-block;
	  font-size: 14px;
	  width: 200px;
	  color: darkgray;
	}
	.hi{
		 border-bottom: 1px solid #ececec;
	}
	/* 信息浏览【赞/关注/被关注数目】开始 */
	.floor_ProfileOption{
		margin-top: 12px;
		margin-bottom: 6px;
		display: flex;
		justify-content: space-around;
		align-items: center;
	}
	.floor_moreOption text{
		padding-right: 5px;
		text-align: center;
		font-size: 14px;
	}
	/* 标签开始 */
	.pr_floor_type {
	  padding-top: 15px;
	  padding-bottom: 25px;
	  display: flex;
	  /* justify-content: center; */
	  justify-content: space-around;
	  align-items: center; 
	}
	
	.pr_floor_type text {
	  padding: 10rpx 15px 12rpx 15px;
	  text-align: center;
	  line-height: 28rpx;
	  display: inline-block;
	  font-size: 12px;
	  height: 20rpx;
	  border-radius: 40rpx;
	  display: inline-block;
	  background-color: #ffd2a2;
	  color: white;
	 

	}
	.pr_floor_typeadd{
		
	}
</style>
